import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify'
import store from './store'
import 'animate.css';

Vue.config.productionTip = false

new Vue({
  router,
  vuetify,
  store,
  beforeCreate() { this.$store.commit('loadStateFromLocalStore');},
  render: h => h(App)
}).$mount('#app')
